  display: flex;
  align-items: center;
  justify-content: space-between;
}

.review_slide--name {
  display: block;
  text-align: center;
  width: 270px;
  height: 54px;
  line-height: 54px;
  background-color: #FFC000;
  font-weight: bold;
  font-size: 24px;
}

.review_slide--time {
  font-size: 14px;
  padding-left: 17px;
  position: relative;
}

.review_slide--time::before {
  content: '';
  position: absolute;
  top: 3px;
  left: 0;
  background-image: url(../img/review-calendar.svg);
  width: 11px;
  height: 11px;
}

.review_slide--review {
  margin-top: 34px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  position: relative;
  padding: 28px 0 37px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.review_slide--review::after {
  content: '';
  position: absolute;
  top: -15px;
  left: 43px;
  background-image: url(../img/review-quote.svg);
  width: 23px;
  height: 20px;
  padding: 0 7px;
  z-index: 1;
}

.review_slide--review::before {
  content: '';
  position: absolute;
  top: -10px;
  left: 33px;
  width: 43px;
  height: 10px;
  background-color: #fff;
}

.review_slide--video {
  margin: 17px auto 22px;
  width: 409px;
  height: 296px;
  background-image: url(../img/video.jpg);
  position: relative;
}

.review_slide--play {
  background-image: url(../img/play-button.svg);
  width: 68px;
  height: 68px;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  transition: .3s all;
}

.review_slide--play:hover {
  box-shadow: 0 0 20px 0 #000;
}

.review_slide--italic {
  font-style: italic;
  font-weight: normal;
  font-size: 16px;
  line-height: 212%;
  text-align: center;
  color: rgba(0, 0, 0, 0.7);
  max-width: 480px;
}

.review_arrow1 {
  position: absolute;
  top: 47%;
  left: -60px;
  transform: translateY(-50%);
  transform: rotate(180deg);
  cursor: pointer;
}

.review_arrow1 path {
  transition: .3s all;
}

.review_arrow1:hover path {
  fill: #FFC000;
}

.review_arrow2 {
  position: absolute;
  top: 50%;
  right: -60px;
  transform: translateY(-50%);
  cursor: pointer;
}

.review_arrow2 path {
  transition: .3s all;
}

.review_arrow2:hover path {
  fill: #FFC000;
}

.review_dots {
  position: absolute;
  bottom: -65px;
  display: block;
  width: 100%;
  padding: 0;
  list-style: none;
  text-align: center;
}

.review_dots li {
  position: relative;
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.review_dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 10px;
  height: 10px;
  padding: 0;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.review_dots li button:hover,
.review_dots li button:focus {
  outline: none;
}

.review_dots li button:hover:before,
.review_dots li button:focus:before {
  opacity: 1;
}

.review_dots li button:before {
  font-size: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  content: '';
  text-align: center;
  background-color: #DEDEDE;
}

.review_dots li.slick-active button:before {
  background-color: #FFC000;
}

.form {
  margin-top: 150px;
  background: url(../img/form.jpg) center / cover no-repeat;
  min-height: 270px;
  position: relative;
}

.form::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 50%;
  height: 100%;
  background-color: #FFC000;
  clip-path: polygon(14% 0, 100% 0, 100% 100%, 0% 100%);
}

.form_row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  position: relative;
  z-index: 1;
}

.form_text {
  color: #fff;
  max-width: 560px;
  margin-top: 70px;
}

.form_text--h3 {
