}

.form_form {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
}

.form_label {
  width: 380px;
  height: 50px;
  position: relative;
}

.form_label1::after {
  content: '';
  position: absolute;
  top: 18px;
  left: 15px;
  background-image: url(../img/input1.svg);
  width: 14px;
  height: 15px;
}

.form_label2 {
  margin-top: 20px;
}

.form_label2::before {
  content: '';
  position: absolute;
  top: 18px;
  left: 15px;
  background-image: url(../img/input2.svg);
  width: 14px;
  height: 14px;
}

.form_input {
  width: 380px;
  height: 50px;
  padding: 0 15px 0 40px;
}

.form_input::placeholder {
  color: rgba(0, 0, 0, 0.6);
}

.form_btn {
  display: block;
  margin: 30px auto 0;
  width: 380px;
  height: 50px;
  background: #171717;
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  border: 1px solid #171717;
  transition: .3s all;
  color: #fff;
}

.form_btn:hover {
  background-color: transparent;
  color: #171717;
}

.footer_top {
